.google_container{
    margin-top: 10px;
}
.google-btn {
    cursor: pointer;
    margin-top: 5px;
    width: 100%;
    height: 52px;
    background-color: $google-blue;
    border-radius: 2px;
    box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
    margin-top: 10px;

    transition: box-shadow .3s ease;

    .google-icon-wrapper {
        position: absolute;
        margin-top: 1px;
        margin-left: 1px;
        width: 50px;
        height: 50px;
        border-radius: 2px;
        background-color: $white;
    }
    .google-icon {
        position: absolute;
        margin-top: 16px;
        margin-left: 16px;
        width: 18px;
        height: 18px;
    }
    .btn-text {
        float: right;
        margin: 18px 30px 0 0;
        color: $white;
        font-size: 14px;
        letter-spacing: 0.2px;
    }
    &:hover {
        box-shadow: 0 0 6px $google-blue;
    }
    &:active {
        background: $button-active-blue;
    }
}